<template>
   <div class="details">
      <Head1 :data='Title'></Head1>
      <div class='sign'>
         <div>{{amount}}</div>
         <span></span>
      </div>
      <div class='deta_cen'>
         <div class='Time'>
            <p>订单编号</p>
            <p>{{Data.otcid}}</p>
         </div>
         <div class='num' >
            <p>{{msg.deal}}个数</p> 
            <span>{{Data.state === 1 ? Data.dst_token_amount : Data.token_amount}} {{Data.state === 1 ? Data.dst_token : Data.from_token}}</span>
         </div>
         <div class='num' style='border-bottom:1px solid #ddd; padding-bottom:30px;'>
            <p>支付个数</p>
            <span>{{Data.state === 1 ? Data.token_amount : Data.dst_token_amount}} {{Data.state === 1 ? Data.from_token : Data.dst_token}}</span>
         </div>
         <div class='message'>
            <p>
               <span>创建人</span>
               <em>{{Data.nickname}}</em>
            </p>
             <p>
               <span>创建时间</span>
               <em>{{Data.create_at*1000 | formatDate}}</em>
            </p>
             <p>
               <span>单价</span>
               <em>{{Data.price}}</em>
            </p>
             <p>
               <span>{{msg.deal2}}个数</span>
               <em>{{Data.state === 1 ? Data.dst_token_amount : Data.token_amount}}</em>
            </p>
         </div>
         <div class='pay' v-if="Data.flag === 2 || Data.flag === 3">
            交易情况
         </div>
         <div class='pay' v-else-if="Data.flag === 1 || Data.flag === 0">
            支付方式
         </div>
         <div class='dingdan' v-if="Data.flag === 2 || Data.flag === 3">
            <div v-if='Data.flag === 2'>
               <p>完成支付,等待对方确认。</p>
            </div>
            <div v-else-if='Data.flag === 3'>
               <p>交易完成,感谢您的配合。</p>
            </div>
         </div>
         <div v-else-if="Data.flag === 1 || Data.flag === 0">
               <div v-if='judge < 0' style='text-align:center;'>
                  <p style='margin: 20px 0; color:red;'>该订单已经过期</p>
                  <van-button size="normal" @click='deletePay()' style='background:#ff7f3b;color:white;border:none;'>删除订单</van-button>
               </div>
               <div v-else-if='judge > 0'>
                     <div v-if="take == null || take == '' ?  ( Data.state === 1 ? Data.from_token === 'CNY' : Data.dst_token === 'CNY' ) : take == 'CNY'">
                     <div class='way' v-if="Data.we_nickname !== null && Data.we_nickname !== ''" @click="deal(1)">
                        <p>
                           <span>
                              <img src="../../../../static/images/icon/weixin.png" alt="">
                           </span>
                           <em>微信</em>
                        </p>
                     </div>
                     <div class='way' v-if="Data.ali_nickname !== null && Data.ali_nickname !== ''" @click="deal(2)">
                        <p>
                           <span>
                              <img src="../../../../static/images/icon/zfb.jpg" alt="">
                           </span>
                           <em>支付宝</em>
                        </p>
                     </div>
                     <div class='way' v-if="Data.idcard !== null && Data.idcard !== ''" @click="deal(3)">
                        <p>
                           <span>
                              <img src="../../../../static/images/icon/yinlian.png" alt="">
                           </span>
                           <em>银联</em>
                        </p>
                     </div>
                  </div>
                  <div class='way' v-else-if="take == null || take == '' ? ( Data.state === 1 ?  Data.from_token ===  Data.from_token :  Data.dst_token ===  Data.dst_token ) : take == take" @click='deal(0)'>
                     <p>
                        <span>
                           <img src="../../../../static/images/background/bi.png" alt="">
                        </span>
                        <em>
                           {{take == null || take == '' ? (Data.state === 1 ? Data.from_token : Data.dst_token) : take}}
                        </em>
                     </p>
                  </div>
               </div>
         </div>
      </div>
      <div class='deal'>
         <div class='deal_bg'></div>
         <div class='deal_cen'>
            <i @click='close()'>X</i>
            <van-cell-group :border='false'>
               <van-field
                  class='input'
                  label="订单编号"
                  disabled
                  :placeholder="res.orderid"
                  @click-right-icon="$toast('question')"/>
            </van-cell-group>
            <van-cell-group :border='false'>
               <van-field
                  class='input'
                  label="卖家"
                  disabled
                  :placeholder="Data.nickname"
                  @click-right-icon="$toast('question')"/>
            </van-cell-group>
            <van-cell-group :border='false'>
               <van-field
                  class='input'
                  label="手机号"
                  disabled
                  :placeholder="Data.phone"
                  @click-right-icon="$toast('question')"/>
            </van-cell-group>
            <van-cell-group :border='false'>
               <van-field
                  class='input'
                  label="支付方式"
                  disabled
                  :placeholder="bi"
                  @click-right-icon="$toast('question')"/>
            </van-cell-group>
            <van-cell-group :border='false'>
               <van-field
                  class='input'
                  label="支付地址"
                  disabled
                  :placeholder="site"
                  @click-right-icon="$toast('question')"/>
            </van-cell-group>
            <div class='btn'>
               <van-button class='btn1' size="small" @click='off()'>取消订单</van-button>
               <van-button class='btn1' size="small" @click='affirm()'>确认支付</van-button>
            </div>
         </div>
      </div>
      <!-- <div class='time'>{{res.time/60}}</div> -->
   </div>
</template>

<script>
import {formatDate} from '../../../common/filters'
import Head1 from '../../Head';
import axios from 'axios';

export default {
   name: 'Details',
   components:{ Head1 },
   data() {
       return {
          Title:'订单信息',
          msg:{
             deal: this.$route.query.num.state === 1 ? '购买' : '出售',
             deal2:this.$route.query.num.state === 1 ? '出售' : '购买'
          },
          arr:[
             { title:'微信' , img:require('../../../../static/images/icon/weixin.png')},
             { title:'支付宝' , img:require('../../../../static/images/icon/zfb.jpg')},
             { title:'银联' , img:require('../../../../static/images/icon/yinlian.png')}
          ],
          Data:'',
          amount:JSON.parse(localStorage.token_set).amount,
          take:this.$route.query.take,
          bi:'',
          site:'',
          res:'',
          Index:this.$route.query.Index,
          judge:this.$route.query.data
       }
   },
   created(){
      this.getData();
   },
   methods:{
      getData(){
         let url = '/otc/getPendOrderInfo';
         this.$http.post(url,{
            accessToken:localStorage.token,
            otcid:this.$route.query.num
         }).then( (res)=>{
            console.log(res);
            this.Data = res;
         })
      },
      deal(num){
         let Deal = document.querySelector('.deal');
         let Time = document.querySelector('.time');
         if( this.Data.myself == 1 ){
            this.$dialog.alert({
               message: '你不能购买或出售自己的订单'
            })
         }else if( this.Data.myself == 0 ){
               let url = '/otc/toPay';
               this.$http.post(url,{
                  cmd:'214',
                  body:{
                     accessToken:localStorage.token,
                     otcid:this.Data.otcid
                  }
               }).then( (res) => {
                  this.res = res;
                  // console.log(res);
               })
               if( num == 1 ){
                  this.bi = '微信';
                  this.site = this.Data.we_account;
               }else if( num == 2 ){
                  this.bi = '支付宝';
                  this.site = this.Data.ali_account;
               }else if( num == 3 ){
                  this.bi = '支付宝';
                  this.site = this.Data.idcard;
               }else if( num == 0 ){
                  this.bi = this.Data.state === 0 ? this.Data.dst_token : this.Data.from_token;
                  this.site = this.Data.address; 
               }
               Deal.style.display = 'block';
         }
      },
      close(){
         let deal = document.querySelector('.deal');
         deal.style.display = 'none';
      },
      affirm(){
         let url = window.globalData.config.domain + '/otc/completePay';
         axios.post(url,{
            cmd:'215',
            body:{
               accessToken:localStorage.token,
               orderid:this.res.orderid
            }
         }).then( (res) => {
            // console.log(res)
            if( res.data.code === 0 ){
               this.$dialog.alert({
                  message: '完成支付，请等待对方确认。'
               })
               this.$router.go(-1);
            }
         })
      },
      off(){
         let url = window.globalData.config.domain + '/otc/cancelPay';
         axios.post(url,{
            cmd:'225',
            body:{
               accessToken:localStorage.token,
               orderid:this.res.orderid
            }
         }).then( (res) => {
            let data = res.data;
            if( data.code === 0 ){
               this.$dialog.alert({
                  message: '订单删除成功。'
               })
               this.$router.go(-1);               
            }
         })
      },
      deletePay(){
         let url =  window.globalData.config.domain + '/otc/deletePay';
         axios.post(url,{
            cmd:'123',
            body:{
               accessToken:localStorage.token,
               orderid:this.$route.query.data.orderid
            }
         }).then( (res) => {
            let data = res.data;
            if( data.code === 0 ){
               this.$dialog.alert({
                  message: '订单删除成功。'
               })
               this.$router.go(-1);
            }
         })
      }
   },
   filters:{
      formatDate(time){
         let data = new Date(time);
         return formatDate(data,'yyyy-MM-dd hh:mm:ss');
      },
    },
}
</script>
 
